import React from "react";
import { useParams } from "react-router-dom";
import { getProjectRelativePath } from "kit";
import "./index.less";
import lltj from "@/assets/img/guanlilei/lltj.png";
import lkpj from "@/assets/img/guanlilei/lkpj.png";
import clzz from "@/assets/img/guanlilei/clzz.png";
import sjtj from "@/assets/img/guanlilei/sjtj.png";
import spdy from "@/assets/img/guanlilei/spdy.png";

const obj = {
  lltj,
  lkpj,
  clzz,
  sjtj,
  spdy,
};
function Manage() {
  const previewParams = useParams();
  const picName = previewParams.name;
  return (
    <div className="manage-wrap">
      <img src={obj[picName]} />
    </div>
  );
}

export default Manage;
